.options {
  border: solid 1px #000000;
  font-family: 'Roboto', sans-serif;
  font-size: 1.2em;
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.content {
  margin: 100px auto 50px auto;
}
.content2 {
  margin: 20px auto;
  /*display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;*/
}
.email a {
  text-decoration: none;
  color: rgb(255, 255, 255);
}
.email a:hover {
  text-decoration: none;
  color: rgb(62, 22, 22);
}
.email a:visited {
  text-decoration: none;
  color: rgb(255, 255, 255);
}
.option {
  margin: 10px 0;
}
.options-title {
  text-align: center;
}
.option-checkbox {
  font-size: 1.25em;
  cursor: pointer;
  margin: 5px 8px;
  display: inline-block;
}
#status {
  font-size: 1.25em;
  margin: 20px 0;
  text-align: center;
}
.save-btn {
  background: #cdd7d1;
  color: #000000;
  border: 0;
  border-radius: 5px;
  padding: 8px 20px;
  font-size: 1em;
  margin: 40px auto; 
  display: block;
  cursor: pointer;
}