.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 75px;
  width: 100%;
  background: #85c17d;
}

.header-title {
  font-size: 1.5em;
  font-weight: bold;
  margin: 0 0 0 10px;
  color: #151c1d;
}
.header-links {
  display: flex;
  flex-direction: row;
  align-items: space-between;
  justify-content: center;
  height: 100%;
  width: 100%;
  margin-top: 20px;
}
